


// horizontal

@mixin tablet-land() {
  @media (max-width: 1250px) {
    @content;
  }
}

@mixin tablet-port() {
  @media (max-width: 950px) {
    @content;
  }
}

@mixin mobile-land() {
  @media (max-width: 780px) {
    @content;
  }
}

@mixin mobile-port() {
  @media (max-width: 560px) {
    @content;
  }
}

// vertical

@mixin short() {
  @media (max-height: 800px) {
    @content;
  }
}

@mixin v-short() {
  @media (max-height: 500px) {
    @content;
  }
}

// cross-axis


@mixin tablet-port-short() {
  @media (max-height: 200px) and (min-width: 560px) {
    @content;
  }
}

@mixin tablet-port-medium() {
  @media (max-height: 600px) and (min-width: 560px) {
    @content;
  }
}

@mixin mobile-port-short() {
  @media (max-height: 800px) and (max-width: 560px) {
    @content;
  }
}


